import React from 'react';
import PropTypes from 'prop-types';

const ExternalLink = ({ externalLink: { name, link } }) => {
  let linkClassName = 'fa fa-' + name;
  return (
    <a
      aria-label='Visit'
      target='_blank'
      rel='noreferrer'
      href={link}
      data-position='top'
      data-tooltip='View Online'
      className='btn-floating btn-large waves-effect waves-light blue-grey tooltipped'
    >
      <i className={linkClassName}></i>
    </a>
  );
};

ExternalLink.propTypes = {
  externalLink: PropTypes.object.isRequired,
};

export default ExternalLink;
